import React, { useState,useEffect } from "react";
import Card from "@material-ui/core/Card";
import TextField from "@mui/material/TextField";
import Switch from "@mui/material/Switch";
import FormControlLabel from "@mui/material/FormControlLabel";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import { useObserver } from "mobx-react";
import IDGenerator from "../../IDGenerator";
import { useModelStore } from "../../modelContext";

function OptionGroupInfoAdd() {
  const modelStore = useModelStore();
  const model = modelStore.model;
  const [id, setID] = useState()
  const [optionGroupInfo, setOptionGroupInfo] = useState({
    id: "",
    name: "",
    title: "",
    grid_size: "",
    type: "",
    sorting: undefined,
    description: "",
    group_title: "",
    show_group_title: false,
    required: false,
    hide_title: false,
    has_additional: false,
  });

  useEffect(() => {
    setID(IDGenerator(model));
  }, []);
  return useObserver(() => (
    <Card
      className="card"
      style={{
        marginLeft: "0px",
        marginTop: "10px",
        outline: "none",
        boxShadow: "none",
      }}
    >
      <div className="option-group-info-container">
        <TextField
          className="option-group-form"
          label="ID"
          value={id}
          disabled
          variant="outlined"
          InputLabelProps={{ shrink: true }}
        />
        <TextField
          className="option-group-form"
          label="Name"
          onChange={(e) => {
            setOptionGroupInfo({ ...optionGroupInfo, name: e.target.value });
          }}
          value={optionGroupInfo.name}
          variant="outlined"
          InputLabelProps={{ shrink: true }}
        />
        <TextField
          className="option-group-form"
          label="Title"
          onChange={(e) => {
            setOptionGroupInfo({ ...optionGroupInfo, title: e.target.value });
          }}
          value={optionGroupInfo.title}
          variant="outlined"
          InputLabelProps={{ shrink: true }}
        />
        <TextField
          className="option-group-form"
          type="number"
          label="Grid Size"
          onChange={(e) => {
            setOptionGroupInfo({
              ...optionGroupInfo,
              grid_size: e.target.value,
            });
          }}
          onWheel={(e) => e.target.blur()}
          value={optionGroupInfo.grid_size}
          variant="outlined"
          InputLabelProps={{ shrink: true }}
        />
        <TextField
          className="option-group-form"
          type="number"
          label="Sorting"
          onChange={(e) => {
            setOptionGroupInfo({ ...optionGroupInfo, sorting: e.target.value });
          }}
          onWheel={(e) => e.target.blur()}
          value={optionGroupInfo.sorting}
          variant="outlined"
          InputLabelProps={{ shrink: true }}
        />
        <FormControl>
          <InputLabel id="type">Type</InputLabel>
          <Select
            labelId="type"
            id="demo-simple-select-helper"
            value={optionGroupInfo.type}
            label="Age"
            onChange={(e) => {
              setOptionGroupInfo({ ...optionGroupInfo, type: e.target.value });
            }}
          >
            <MenuItem value={"single"}>Single</MenuItem>
            <MenuItem value={"multi"}>Multi</MenuItem>
            <MenuItem value={"singleImage"}>Single Image</MenuItem>
            <MenuItem value={"multiImage"}>Multi Image</MenuItem>
            <MenuItem value={"picker"}>Picker</MenuItem>
          </Select>
        </FormControl>
        <div className="form-control-label-container">
          <FormControlLabel
            className="form-control-label"
            label="Required"
            labelPlacement="top"
            control={
              <Switch
                checked={optionGroupInfo.required}
                onChange={(e, checked) => {
                  setOptionGroupInfo({
                    ...optionGroupInfo,
                    required: e.target.checked,
                  });
                }}
              />
            }
          />
          <FormControlLabel
            className="form-control-label"
            label="Hide Title"
            labelPlacement="top"
            control={
              <Switch
                checked={optionGroupInfo.hide_title}
                onChange={(e, checked) => {
                  setOptionGroupInfo({
                    ...optionGroupInfo,
                    hide_title: e.target.checked,
                  });
                }}
              />
            }
          />
          <FormControlLabel
            className="form-control-label"
            label="Has Additional"
            labelPlacement="top"
            control={
              <Switch
                checked={optionGroupInfo.has_additional}
                onChange={(e, checked) => {
                  setOptionGroupInfo({
                    ...optionGroupInfo,
                    has_additional: e.target.checked,
                  });
                }}
              />
            }
          />
        </div>
        <TextField
          className="option-group-form"
          label="Description"
          onChange={(e) => {
            setOptionGroupInfo({
              ...optionGroupInfo,
              description: e.target.value,
            });
          }}
          value={optionGroupInfo.description}
          variant="outlined"
          InputLabelProps={{ shrink: true }}
          multiline
          rows={4}
        />
        <div>
          <TextField
            className="option-group-form"
            label="Group Title"
            onChange={(e) => {
              setOptionGroupInfo({
                ...optionGroupInfo,
                group_title: e.target.value,
              });
            }}
            value={optionGroupInfo.group_title}
            variant="outlined"
            InputLabelProps={{ shrink: true }}
          />
          <FormControlLabel
            className="form-control-label"
            label="Show Group Title"
            labelPlacement="end"
            control={
              <Switch
                checked={optionGroupInfo.show_group_title}
                onChange={(e, checked) => {
                  setOptionGroupInfo({
                    ...optionGroupInfo,
                    show_group_title: e.target.checked,
                  });
                }}
              />
            }
          />
        </div>
      </div>
    </Card>
  ));
}

export default OptionGroupInfoAdd;
